import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getPrioritizeObjects, deletePrioritizeObject } from 'services/prioritizeObject';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';

class ListPrioritizeObjectPage extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deletePrioritizeObject(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const columns = [
      {
        title: 'Mã',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.DANH_MUC__DOI_TUONG__UU_TIEN__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    const { reload } = this.state;
    return (
      <Card title="Đối tượng ưu tiên">
        <CartHeadAction>
          <Link to={routeConstant.DANH_MUC__DOI_TUONG__UU_TIEN__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getPrioritizeObjects}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListPrioritizeObjectPage />
  </Page>
);



